.alert {
  display: none;
}
.alert.active {
  display: flex;
  flex-direction: row;
  padding: 15px;
  border: 1px solid #fff;
  background: rgba(255, 255, 255, 0.6);
  border-radius: 8px;
  justify-content: space-between;
  align-items: center;
}

.message {
  margin: 0;
}

.button {
  width: 30px;
  height: 30px;
  border: 0;
  border-radius: 6px;
  padding: 0;
  margin: 0;
  line-height: 30px;
  background: #fff;
  font-size: 16px;
}
.button:hover {
  background: rgba(13, 101, 141, 0.2);
  color: #fff;
}

